body {
  font-family: 'Indie Flower', cursive;
  background: rgb(0, 131, 209);
  color: #323b40;
  margin: 0;
}

.content {
  width: 60%;
  margin: 0 auto;
}

.floating-action-button {
  bottom: 0;
  position: fixed;
  margin: 1em;
  right: 0;
}

.home-button {
  color: white;
  background: black;
  box-shadow: 0px 5px 11px -2px rgba(0, 0, 0, 0.18), 
              0px 4px 12px -7px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  display: block;
  width: 56px;
  height: 56px;
  margin: 20px auto 0;

  text-align: center;
  position: relative;
  transition: all .1s ease-out;
}
.home-button .back-icon  {
  position:relative;
  top: calc(50% - 10px);
}

.home-button:active, 
.home-button:focus, 
.home-button:hover {
  box-shadow: 0 0 4px rgba(0,0,0,.14),
    0 4px 8px rgba(0,0,0,.28);
}

/* phones */
@media only screen and (max-width: 767px) {
  .content {
    width: 90%;
    margin: 0 auto;
  }
}

.section__title {
	margin: 25px 0;
	border-bottom: 1px solid white;
}

.chevron-button {
	position: relative;
	display: inline-block;
	font-size: 1.4rem;
	padding: 20px 40px;
	background-color: rgb(59, 194, 194);
	border: solid 1px #555;
	color: white;
	background-size: 20px 20px;
	transition: all 0.5s ease;
}

.chevron-button::after {
	position: absolute;
	top: 30%;
    right: 0.6em;
	content: "\BB";
	transition: all 0.5s ease;
	opacity: 0;
}

.chevron-button:hover {
	padding: 20px 60px 20px 20px;
}

.chevron-button:hover::after {
	right: 1.2em;
	opacity: 1;
}

.expanding-button__list{
	list-style: none;
}

.expanding-button__item {
	background: #fff;
	height: 40px;
	line-height: 40px;
	color: #666;
	width: 100%;
}

.expanding-button__item span {
	background: #e74c3c;
	height: 40px;
	margin-right: 20px;
	text-align: center;
	color: white;
	width: 0;
	display: inline-block;
	opacity: 0;
	transition: all 0.5s ease;
	cursor: pointer;
}

.expanding-button__item:hover span {
	width: 40px;
	opacity: 1;
}

.expanding-button__item span:hover {
	background: #d13929;
	color: rgb(236, 236, 236);
}
/*
* === BASE LOGIN - Some of this should be placed on body tag ===
*/
.login {
    background: #696969;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #b9b9b9, #696969);  /* Chrome 10-25, Safari 5.1-6 */
    background-image: linear-gradient(
        to right bottom,
        rgba(199, 199, 199, 0.6),
        rgba(112, 112, 112, 0.6)),
        url(/ch_ui_kit/static/media/donuts.d270ffd3.png);
    background-size: contain;
    background-position: top;
    color: white;
    font-family: Lato, sans-serif;
    width: 100%;
    height: 100vh;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
}

/*
* === Flexbox based layout ===
*/
.login__row {
    display: flex;
    flex-wrap: wrap;
}

.login__center {
    justify-content: center;
    text-align: center;
}

/*
* === Top header and subheader ===
*/
.login__text {
    margin: 5% 0 3% 0;
    text-shadow: 1px 1px black;
    flex-basis: 83.33333333%;
    max-width: 83.33333333%;
}

/* tablets and desktop */
@media only screen and (min-width: 768px) {
    .login__text {
        flex-basis: 50%;
        max-width: 50%;
    }
}

/*
* === Login box ===
*/
.login__box {
    background: white;
    border-radius: 10px;
    min-height: 100px;
    color:black;
    flex-basis: 83.33333333%;
    max-width: 83.33333333%;
}

/* tablets and desktop */
@media only screen and (min-width: 768px) {
    .login__box {
        flex-basis: 33.33333333%;
        max-width: 33.33333333%;
    }
}

.login-form {
    padding: 50px 50px 25px 50px;
    text-align: left;
    color: rgb(70, 70, 70);
}

.login-form__header {
    color: rgb(60, 60, 60);
}

.login-form__item {
    margin-bottom: 25px;
}

.login-form__label {
    position: relative;
    display: block;
    margin-bottom: 0;
    line-height: 40px;
    font-size: 13px;
    font-weight: 300;
}

.login-form__input-wrapper {
    position: relative;
    color: #b6b5b5;
}

.login-form__input-wrapper i {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 17px;
    z-index: 100;
}

.login-form__input-wrapper > i + .login-form__input {
    text-indent: 32px;
}

.login-form__input {
    display: block;
    border: 1px solid rgb(196, 195, 195);
    width: 100%;
    border-radius: 5px;
    /* line-height: 1.5; */
    height: 35px;
    color: rgb(100, 100, 100);
    font-weight: 400;
    font-size: .9rem;
}

.login-form__input::-webkit-input-placeholder {
    color: #c4c2c2;
    font-weight: 300;
    font-size: .8rem;
}

.login-form__input::-ms-input-placeholder {
    color: #c4c2c2;
    font-weight: 300;
    font-size: .8rem;
}

.login-form__input::placeholder {
    color: #c4c2c2;
    font-weight: 300;
    font-size: .8rem;
}

.login-form__item_forgot {
    display: flex;
    justify-content: flex-end;
}

.login-form__forget{
    text-decoration: none;
    color: #0190fe;
    font-size: 12px;
    font-weight: 400;
}

.login-form__actions{
    border-top: 1px solid rgb(200, 200,200);
    /* margin-top: 15px; */
    padding: 15px 0 15px 0;
}

.login-form__submit{
    color: white;
    background-color: #0190fe;
    outline: 0;
    border-radius: 3px;
    line-height: 1.5;
    text-align: center;
    padding: 0 15px;
    height: 30px;
    font-size: 15px;
    border: 1px solid transparent;
    margin: 0 7px 0 0;
}

.login-form__register {
    color: #8d8c8c;
    font-size: 13px;
}

.login-form__register__link {
    text-decoration: none;
    color: #0190fe;
    border-bottom: 1px solid #c4c2c2;
    padding-bottom: 2px;
}

