.item-card {
    position: relative;
    background:#323b40;
    color: white;
    max-width: 960px;
    margin: 90px 0 25px 0;
    padding: 5px 40px 50px;
    box-sizing: border-box;
    border-radius: 2px;
    box-shadow: 5px 5px 50px 0px #fa90a3;
    /* min-height: 500px; */
    font-size: 20px;
    line-height: 28px;
  }

  .item-card .item-card__title {
    font-size: 1.4rem;
    display: inline-block;
    /* transform: rotate(-90deg); */
    transition: all ease-in-out .1s;
    position: absolute;
    top: -40px;
    left: 10px;
    background: #e12345;
    color: white;
    margin: 0;
    line-height: 1.6rem;
    padding: 15px;
    z-index: -2;
    border-radius: 4px;
    min-width: 130px;
  }

  .item-card__title small{
    font-weight: normal;
    font-size: 1rem;
    display: block;
    color: #323b40;
  }

  .item-card:hover .item-card__title {
    top: -65px;
  }

  .item-card__contents {
    margin: 25px 25px;
  }

  .item-card__source {
    bottom: 0;
    position: absolute;
    margin: 1em;
    left: 0;
    font-size: 0.7rem;
    padding: 0 0;
    transition: all 0.5s ease;
    cursor: pointer;
    color: white;
    text-decoration: none;
  }

  .item-card__source::before {
    content: "\BB";
    top: 0%;
    left: -0.1em;
    position: absolute;
    font-size: 0.7rem;
    opacity: 0;
    transition: all 0.5s ease;
  }

  .item-card__source:hover::before{
    opacity: 1;
  }

  .item-card__source:hover{
    padding-left: 10px;
  }

  .item-card__github {
    position: relative;
    margin-left: 5px;
  }
